@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Manjari&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Manjari&display=swap);
*{box-sizing:border-box}body{font-family:'Catamaran', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:radial-gradient(900px at 100% 110%, #212750 10%, #020409 100%);height:100vh}html,body,#root{width:100%;height:100%;margin:0;padding:0;overflow:hidden}.app{width:100%;height:100%;overflow-x:hidden}.anim{width:100vw;height:100vh;position:absolute;z-index:1}.landingPage{color:#fff;text-align:left;font-size:40px;z-index:5;width:100%;height:100vH;display:flex;align-items:center;justify-content:center}.landingPage .text-wrapper{width:1000px;padding-left:10rem;display:flex;align-items:center;flex-direction:column;text-align:left}.landingPage .text-wrapper p{width:100%;font-family:'Catamaran', sans-serif;text-align:left;font-size:40px;margin-bottom:-30px;font-weight:500;text-shadow:0px 3px 2px #000;color:#ececec}.landingPage .text-wrapper h1{width:100%;background-image:linear-gradient(110deg, rgba(85,101,203,0.5) 0%, #fff 100%);color:transparent;-webkit-background-clip:text;font-size:80px;margin:0}@media (max-width: 70rem){body{background:radial-gradient(800px at 120% 120%, #212750 10%, #020409 100%)}.landingPage .text-wrapper{padding-left:5rem}.landingPage .text-wrapper p{font-size:25px;margin-bottom:-18px}.landingPage .text-wrapper h1{font-size:50px}}@media (max-width: 40rem){.landingPage .text-wrapper{padding-left:3rem}.landingPage .text-wrapper p{font-size:25px;margin-bottom:-14px}.landingPage .text-wrapper h1{font-size:35px}}@media (max-width: 30rem){.landingPage .text-wrapper{padding-left:1rem}}.particles{position:absolute;top:0;left:0;bottom:0;right:0;z-index:-99999}::-webkit-scrollbar{width:4px}::-webkit-scrollbar-track{box-shadow:inset 0 0 5px rgba(73,73,73,0.3);border-radius:10px}::-webkit-scrollbar-thumb{background:#fff;border-radius:10px}

header{position:absolute;z-index:99;width:100%;padding:30px;background-color:rgba(250,250,250,0.05);-webkit-backdrop-filter:blur(9px);backdrop-filter:blur(9px);font-size:20px}header .header-inner{display:flex;align-items:center;justify-content:space-between}header .header-inner nav ul{margin:0;padding:0;display:flex}header .header-inner nav ul li{list-style:none}header .header-inner nav ul li p{text-transform:capitalize;margin:0 40px;color:#ececec;transition:all 0.5s ease}header .header-inner nav ul li p:hover{color:#a2b3dd;transition:all 0.5s ease}@media (max-width: 85rem){header{display:flex;align-items:center;justify-content:space-evenly}}

.timeline-container{margin-bottom:50px;width:100%}.timeline{padding-top:0rem;font-family:'Catamaran', sans-serif;font-size:20px;color:white;padding-bottom:3rem}.timeline h3{font-size:22px}.timeline h5{font-family:'Abel', sans-serif;margin-top:-10px}.vertical-timeline-element-content{background:linear-gradient(110deg, rgba(85,101,203,0.2) 0%, rgba(255,255,255,0.1) 100%);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);border:2px solid rgba(255,255,255,0);text-shadow:0px 0px 0px rgba(255,255,255,0);box-shadow:0 8px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.15);transition:border 0.8s ease-in-out}.vertical-timeline-element-content:hover{border:2px solid rgba(255,255,255,0.2);transition:border 0.5s ease-in-out}.vertical-timeline-element-content-arrow{border-right:8px solid rgba(195,195,198,0.4)}.vertical-timeline::before{background:rgba(255,255,255,0);border-radius:100px}.vertical-timeline-element-icon{box-shadow:0 0 0 1.5px #fff,0px 0px 20px 0 rgba(255,255,255,0.35)}@media (max-width: 73rem){.vertical-timeline::before{background:rgba(255,255,255,0)}}

.custom-btn{background:linear-gradient(110deg, rgba(85,101,203,0.5) 0%, rgba(160,173,254,0.5) 100%);border:none;z-index:1;width:180px;height:auto;color:#fff;border-radius:5px;padding:10px 25px;font-weight:900;cursor:pointer;transition:all 0.3s ease;position:relative;outline:none;margin-left:0rem;margin-top:10px;text-decoration:none;display:flex;align-items:center;flex-direction:column}.custom-btn p{margin:0}.custom-btn:after{position:absolute;content:"";width:0;height:100%;top:0;right:0;z-index:-1;background-color:#dedede;border-radius:5px;color:rgba(85,101,203,0.5);transition:all 0.5s ease}.custom-btn:hover{color:#5565cb}.custom-btn:hover:after{left:0;width:100%}.custom-btn:active{top:2px}@media (max-width: 85rem){.custom-btn{font-size:0.9rem;width:150px}}

.custom-title{text-align:center;background-image:linear-gradient(110deg, rgba(85,101,203,0.5) 0%, #fff 100%);color:transparent;-webkit-background-clip:text;font-size:100px;padding:0rem;-webkit-margin-after:0rem;margin-block-end:0rem;-webkit-margin-before:0rem;margin-block-start:0rem;max-width:1000px}@media (max-width: 100rem){.custom-title{font-size:80px}}@media (max-width: 85rem){.custom-title{font-size:50px}}

.about{width:100%;background-color:rgba(250,250,250,0.05);-webkit-backdrop-filter:blur(8px);backdrop-filter:blur(8px);margin-bottom:100px;padding-bottom:20px;color:#FFF;display:flex;align-items:center;flex-direction:column}.about-grid-container{width:auto;display:grid;grid-template-columns:auto;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem;grid-template-areas:'profilephoto aboutmetitle' 'profilephoto aboutmetext' 'profilephoto cvbutton';justify-items:stretch;text-align:justify;padding:3rem}.about-profile-photo{grid-area:profilephoto;display:flex;align-items:center;flex-direction:column;width:100%;text-align:left;justify-content:center}.about-profile-photo img{border-radius:3%;box-shadow:0 4px 18px 0 rgba(0,0,0,0.2),0 16px 40px 0 rgba(0,0,0,0.9);background-size:cover;background-position:center;width:100%;height:100%;max-width:250px;max-height:250px}.about-title{grid-area:aboutmetitle;margin-bottom:-10px}.about-title h1{text-align:left}.about-description{grid-area:aboutmetext;text-align:justify}.about-description p{margin:0;max-width:600px}.about-btn{grid-area:cvbutton}@media (max-width: 85rem){.about-grid-container{grid-column-gap:0rem;-webkit-column-gap:0rem;column-gap:0rem;width:auto;grid-template-areas:'aboutmetitle' 'aboutmetext' 'cvbutton'}.about-grid-item{text-align:left}.about-profile-photo img{display:none}.about-description{text-align:justify}.about-description p{font-size:15px}}

.social-container{background-color:rgba(250,250,250,0.05);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);color:white;padding:1px 40px;margin-top:5rem;text-align:center;font-size:17px}a.social{margin:0 1rem;margin-top:1rem;transition:-webkit-transform 250ms;transition:transform 250ms;transition:transform 250ms, -webkit-transform 250ms;display:inline-block;color:#ffffff}a.social:hover{-webkit-transform:translateY(-4px);transform:translateY(-4px)}.social-container hr{margin-top:1.5rem;width:350px;border:1px solid rgba(255,255,255,0.3)}.social-container p{color:rgba(255,255,255,0.9)}@media (max-width: 85rem){.social-container{font-size:16px}}@media (max-width: 50rem){.social-container{font-size:14px}.social-container hr{width:50%;min-width:250px}}

.layers{width:100%;max-width:600px;margin:auto auto;padding:2rem;overflow:visible;display:flex;align-items:center;padding-bottom:5rem;padding-top:3.5rem}.layers g{-webkit-transform:translateX(var(--offset-x)) translateY(var(--offset-y)) translateZ(var(--offset-z));transform:translateX(var(--offset-x)) translateY(var(--offset-y)) translateZ(var(--offset-z));transition:-webkit-transform 0.2s ease-out;transition:transform 0.2s ease-out;transition:transform 0.2s ease-out, -webkit-transform 0.2s ease-out;-webkit-animation:hover 2s infinite;animation:hover 2s infinite}.layers g:nth-of-type(1):hover,.layers g:nth-of-type(2):hover,.layers g:nth-of-type(3):hover,.layers g:nth-of-type(4):hover,.layers g:nth-of-type(5):hover,.layers g:nth-of-type(6):hover,.layers g:nth-of-type(7):hover,.layers g:nth-of-type(8):hover,.layers g:nth-of-type(9):hover,.layers g:nth-of-type(10):hover,.layers g:nth-of-type(11):hover,.layers g:nth-of-type(12):hover{-webkit-transform:translateX(var(--offset-x)) translateY(calc(var(--offset-y) - 2px)) translateZ(var(--offset-z));transform:translateX(var(--offset-x)) translateY(calc(var(--offset-y) - 2px)) translateZ(var(--offset-z))}.layers text{font-weight:bold;-webkit-transform:skew(-68deg, 22deg) translate(calc(49px * var(--size) / 100), calc(7px * var(--size) / 100)) scaleY(0.5);transform:skew(-68deg, 22deg) translate(calc(49px * var(--size) / 100), calc(7px * var(--size) / 100)) scaleY(0.5);text-shadow:0px 0px 20px rgba(255,255,255,0.8);fill:#fff}.layers path:nth-child(2){fill:rgba(0,0,0,0.5)}.layers path:nth-child(3){fill:rgba(255,255,255,0.3)}

.gameDetailsRightContainer,.gameDetailsLeftContainer{width:100%;height:100%}.gameDetailsLeft,.gameDetailsRight{color:#fff;width:50%;max-width:50%;height:100%;padding-left:2rem;padding-right:2rem;flex-direction:row;display:flex;align-items:center;justify-content:left}.gameDetailsLeft p,.gameDetailsRight p{font-size:1.3rem;text-align:justify;max-width:400px}.gameDetailsLeft .game-btn,.gameDetailsLeft .game-description,.gameDetailsRight .game-btn,.gameDetailsRight .game-description{display:flex;flex-direction:column}.gameDetailsLeft .game-title,.gameDetailsRight .game-title{margin-bottom:-40px}.gameDetailsLeft .game-content,.gameDetailsRight .game-content{background-color:rgba(250,250,250,0.05);-webkit-backdrop-filter:blur(8px);backdrop-filter:blur(8px);border-radius:10px 10px 10px 10px;padding:3rem;width:50vW}.gameDetailsLeft{justify-content:left;float:right}.gameDetailsLeft p,.gameDetailsLeft h1{text-align:left}.gameDetailsLeft .game-btn,.gameDetailsLeft .game-description{align-items:left}.gameDetailsRight{justify-content:right;align-items:right;float:left}.gameDetailsRight .custom-title{background-image:linear-gradient(-110deg, rgba(85,101,203,0.5) 0%, #fff 100%)}.gameDetailsRight p,.gameDetailsRight h1{text-align:right}.gameDetailsRight .game-btn,.gameDetailsRight .game-description{align-items:flex-end}.scrollArea{position:absolute;top:0;left:0;width:100vw;height:100vh;overflow-y:scroll;overflow-x:hidden}.loading{position:absolute;top:0px;left:0px;width:100%;height:100%;background:#000000;display:flex;align-items:center;justify-content:center;z-index:1000}.loading-bar-container{width:100px;height:20px;border-radius:5px;background:#272727}.loading-bar{height:20px;border-radius:5px;background:#ffffff}@media (max-width: 1440px){.gameDetailsRight .game-title,.gameDetailsLeft .game-title{margin-bottom:-20px}.gameDetailsRight p,.gameDetailsLeft p{font-size:1rem}}@media (max-width: 900px){.gameDetailsLeftContainer,.gameDetailsRightContainer{background:rgba(0,0,0,0);height:100%}.gameDetailsLeft,.gameDetailsRight{padding-top:0rem;font-size:1rem;width:100%;max-width:100%;display:grid;float:none;justify-content:center;float:center}.gameDetailsLeft h1,.gameDetailsRight h1{text-align:center}.gameDetailsLeft .game-btn,.gameDetailsLeft .game-description,.gameDetailsRight .game-btn,.gameDetailsRight .game-description{align-items:center}.gameDetailsLeft .game-content,.gameDetailsRight .game-content{padding:3rem;padding-bottom:2rem;width:100vW}.gameDetailsLeft .game-content p,.gameDetailsRight .game-content p{text-align:justify}}

